//react init
import React from "react";
import ReactDOM from "react-dom";
//router
import AppRouter from "./routers/AppRouter";
//store
import { Provider } from "react-redux";
import configureStore from "./store/configureStore";
import addItem from "./actions/portfolio";
//css
import "normalize.css/normalize.css";
import "./styles/styles.scss";

const store = configureStore();

store.subscribe(() => {
  store.getState();
});

const projetOne = store.dispatch(
  addItem({
    id: 1,
    name: "My Portfolio",
    src: "https://i.imgur.com/IYeCBzh.png",
    alt: "Creation of my website",
    href: "project/id/",
    desc: "Creation of my website",
    descMore:
      "Wanna take a look at how i did my portfolio ? Please feel free to check my code !",
    btn: "Check my code on Github",
    tools: "#VUE-JS, #VUEX, #Tailwind",
    link: "http://www.google.fr",
  })
);

const app = document.getElementById("app");

const jsx = (
  <Provider store={store}>
    <AppRouter />
  </Provider>
);

ReactDOM.render(jsx, app);
